<template>
  <div class="left">
    <div class="w-input-wrap">
      <input
        v-model="searchCon"
        @keyup.enter="searchBtn"
        class="w-search-input"
        placeholder="人员索引"
        type="text"/>
      <img class="w-search" @click="searchBtn" src="../img/search-icon.png" alt="" srcset="">
    </div>
    <btn4 class="tab" @change="tabClick"></btn4>
    <div class="list">
      <el-scrollbar style="height:100%">
        <ul>
          <li
            :class="curNameId===item.value?'active':''"
            v-for="(item,index) of data"
            :key="index"
            @click="handleClick(item)"
          >{{item.name}}
          </li>
        </ul>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
  import btn4 from "@/components/btnChange/btn4";

  export default {
    name: "left",
    components: {
      btn4
    },
    props: {
      data: Array
    },
    data() {
      return {
        curNameId: 0,
        searchCon: '',
      }
    },
    // mounted() {
    //   this.curNameId = this.data[0].value;
    // },
    methods: {
      // 点击按钮
      tabClick(name) {
        this.$emit("typeChange", name);
      },
      // 点击姓名
      handleClick(item) {
        this.curNameId = item.value;
        this.$emit("change", this.curNameId);
      },
      // 点击搜索
      searchBtn() {
        this.$emit("seachChange", this.searchCon);
      },
    },
    watch: {
      data: {
        handler(newVal, oldVal) {
          this.curNameId = this.data[0].value;
        },
        immediate:true,
        deep: true
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .left
    position relative
    padding-left 30px

    .w-input-wrap
      display flex
      align-items center
      border 1px solid #0fddfb
      line-height 0
      width 90%
      height 36px
      background #0c1e3f
      border-radius 20px

      .w-search-input
        width 100%
        background transparent
        border none
        outline none
        font-size 14px
        padding-left 20px
        color #fff
        line-height 34px

        &::-webkit-input-placeholder
          color #5591ff

      .w-search
        width 30px
        margin 0 20px
        cursor pointer

        &:hover
          transform scale(0.9)
          transition 0.5s transform

    .tab
      margin 10px 0

    .list
      height 630px

      ul
        width 90%

        li
          height 32px
          font-size 18px
          color #9aedfb
          line-height 32px
          margin 10px 0
          text-align center
          border-radius 5px
          border 1px solid transparent
          cursor pointer

          &.active
            border 1px solid #ffc37d
            color #fff
            font-weight 700
            text-shadow 0 0 10px #276bf4
</style>
