<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <left v-if="seachList.length" :data="seachList" @change="leftNameClick" @typeChange="leftTypeClick"
              @seachChange="leftSeachClick"></left>
      </div>
      <div class="w-right">
        <div class="w-r-l">
          <ctitle :title="'基本信息'"></ctitle>
          <div class="inner" v-loading="!jbxxData" :element-loading-background="$STANDARD.loadingBgColor">
            <jbxx v-if="jbxxData" :data="jbxxData"></jbxx>
          </div>
        </div>
        <div class="w-r-r">
          <div class="top">
            <div class="left">
              <ctitle :title="'信访场所分布'"></ctitle>
              <div class="inner">
                <radar :params="xfcsRadarParams"></radar>
              </div>
            </div>
            <div class="right">
              <ctitle :title="'投诉问题分布'"></ctitle>
              <div class="inner">
                <loopPie :params="tswtPieParams"></loopPie>
              </div>
            </div>
          </div>
          <div class="btm">
            <ctitle :title="'信访轨迹分析'"></ctitle>
            <div class="inner" v-loading="!xfgjData" :element-loading-background="$STANDARD.loadingBgColor">
              <xfgj ref='xfgj' v-if="xfgjData" :data="xfgjData"></xfgj>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import radar from "@/components/radar/radar";
  import loopPie from "@/components/pie/LoopPie";
  import xfgj from "../component/xfgj";
  import jbxx from "../component/jbxx";
  import left from "../component/left";

  export default {
    name: "lmwsts",
    components: {
      ctitle,
      radar,
      loopPie,
      xfgj,
      jbxx,
      left
    },
    data() {
      return {
        cardId: '',
        // 1查询列表
        seachList: [],
        seachType: "drop",
        seachName: "drop",
        // 2.基本信息
        jbxxData: null,
        //3.信访场所分布
        xfcsRadarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            center: ['50%', '50%'],
            radius: '65%'
          }
        },
        //4投诉问题占比
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        //5.信访轨迹分析
        xfgjData: null
      }
    },
    mounted() {
      this.getLeftAPI(this.date, this.seachType, this.seachName);
    },
    methods: {
      getLeftAPI(date, type, name) {
        // 1左侧查询
        get_http("qjywjkXfrhxLeftSeach", {
          day: `[${date.startDate},${date.endDate}]`,
          xfxs: type,
          'LIKE-xm': name
        })
          .then(res => {
            this.seachList = res.data.data;
            if (this.seachList.length) {
              this.cardId = this.seachList[0].value;//默认选中第一个人员
            } else {
              this.cardId = '';
            }
            this.getRightAPI(date, this.cardId);
          })
          .catch(err => {
            console.log(err);
          });
      },
      getRightAPI(date, cardId) {
        // 2.基本信息
        get_http("qjywjkXfrhxJbxx", {sfzhm: cardId})
          .then(res => {
            this.jbxxData = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.信访场所分布
        get_http("qjywjkXfrhxXfcsRadar", {day: `[${date.startDate},${date.endDate}]`, sfzhm: cardId})
          .then(res => {
            this.xfcsRadarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4投诉问题占比
        get_http("qjywjkXfrhxZbPie", {day: `[${date.startDate},${date.endDate}]`, sfzhm: cardId})
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访轨迹分析
        get_http("qjywjkXfrhxXfgjTimeLine", {day: `[${date.startDate},${date.endDate}]`, sfzhm: cardId})
          .then(res => {
            this.xfgjData = res.data;
            console.log(this.xfgjData);
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 左侧姓名
      leftNameClick(cardId) {
        console.log(cardId);
        this.getRightAPI(this.date, cardId);
      },
      // 左侧按钮
      leftTypeClick(type) {
        this.seachType = type;
        this.getLeftAPI(this.date, this.seachType, this.seachName);
      },
      // 左侧搜索
      leftSeachClick(name) {
        this.seachName = name;
        this.getLeftAPI(this.date, this.seachType, this.seachName);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getLeftAPI(val, this.seachType, this.seachName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .inner
        height calc(100% - 0.28rem)
      .w-left
        width 3rem
        height 100%
        position relative
        border-right 1px #91e3f7 dashed
      .w-right
        flex 1
        display flex
        .w-r-l
          width 530px
        .w-r-r
          flex 2
          overflow hidden
          display flex
          flex-direction column
          .top
            flex 1
            display flex
            .left
              flex 1
            .right
              flex 1
          .btm
            flex 1.3
</style>
