<template>
  <div class="jbxx">
    <div class="inner">
      <div class="photo">
        <div class="c-img">
          <img :src="list[getKey('头像')]" alt="">
        </div>
      </div>
      <div class="name">{{list[getKey('姓名')]}}</div>
      <!--<div class="type">-->
        <!--<ul>-->
          <!--<li :class="item.value===0?'yellow':'red'" v-for="(item,index) of data.typeData" :key="index">{{item.name}}-->
          <!--</li>-->
        <!--</ul>-->
      <!--</div>-->
      <div class="list">
        <ul>
          <li v-for="(item,index) of Object.keys(data.map).slice(2)" :key="index">
            <p class="name">{{data.map[item]}} : </p>
            <p class="value" :title="list[item]">{{list[item]}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "jbxx",
    props: {
      data: Object
    },
    methods: {
      getKey(name) {
        for (let i in this.data.map) {
          if (this.data.map[i] === name) {
            return i
          }
        }
      }
    },
    computed: {
      list() {
        return this.data.data[0]
      },
    },
  }
</script>

<style lang="stylus" scoped>
  .jbxx
    position relative
    width 100%
    height 100%
    .inner
      .photo
        margin 20px 0
        text-align center
        .c-img
          width 1.8rem
          height 1.8rem
          margin 0 auto
          border 8px solid transparent
          box-shadow 0 0 20px #08c1d8
          border-radius 50%
          overflow hidden
          img
            display block
            width 100%
            height 100%
      > .name
        font-size 0.3rem
        margin 0.5rem 0
        color #fff
        text-align center
        background url("../img/name-bg.png") no-repeat center center
      .type
        margin-top 20px
        ul
          width 340px
          display flex
          justify-content center
          flex-wrap wrap
          margin 0 auto
          li
            width 97px
            height 34px
            margin 8px
            font-size 16px
            color #fff
            line-height 32px
            text-align center
            &.yellow
              background url("../img/type-bg-yellow.png") no-repeat
            &.red
              background url("../img/type-bg-red.png") no-repeat
      .list
        ul
          margin-left 130px
          li
            display flex
            line-height 40px
            font-size 0.18rem
            .name
              color #59baf2
              white-space nowrap
              margin-right 10px
            .value
              color #fff
              overflow hidden
              text-overflow ellipsis
              white-space nowrap
</style>
