<template>
  <div class="xfgj">
    <div class="container">
      <ul class="list clearfloat">
        <li class="item-li" v-for="(item,index) of list" :key="index">
          <div class="inner" :class="popShowIndex===index?'active':''">
            <p class="time">重复次数：{{item[getKey('重复信访次数')]}}</p>
            <i class="icon"></i>
            <p class="date">{{item[getKey('信访日期')]}}</p>
            <i class="circle" @click="timeChange(index,item[getKey('信访事项id')])"></i>
          </div>
          <transition name="fade">
            <div>
              <div class="arrow" v-if="popShowIndex===index"></div>
              <ul class="pop clearfloat" v-if="popShowIndex===index&&detailData">
                <li class="pop-li" v-for="(it,i) of Object.keys(detailData.map)" :key="i">
                  <p class="name">{{detailData.map[it]}} : </p>
                  <p class="value" :title="it.value">{{detailData.data[0][it]}}</p>
                </li>
              </ul>
            </div>
          </transition>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";

  export default {
    name: "xfgj",
    props: {
      data: Object
    },
    data() {
      return {
        xfsxid: "",
        popShowIndex: 3,
        detailData: null
      }
    },
    mounted() {
      this.popShowIndex=this.list.length-1;
      this.xfsxid=this.list[this.popShowIndex][this.getKey('信访事项id')];
      this.getDetailAPI(this.xfsxid);
    },
    methods: {
      getDetailAPI(id) {
        get_http("qjywjkXfrhxXfgjDetail", {xfsxid: id})
          .then(res => {
            this.detailData = res.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 时间切换
      timeChange(index,id) {
        this.xfsxid=id;
        this.popShowIndex = index;
        this.getDetailAPI(this.xfsxid);
      },
      // 时间轴
      getKey(name) {
        for (let i in this.data.map) {
          if (this.data.map[i] === name) {
            return i
          }
        }
      }
    },
    computed: {
      list() {
        return this.data.data
      },
      styleType() {
        return this.$store.state.styleType;
      }
    },
    watch: {
      list: {
        handler(newVal, oldVal) {
          this.xfsxid=newVal[this.popShowIndex][this.getKey('信访事项id')];
          this.getDetailAPI(this.xfsxid);
        },
        deep: true
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .xfgj
    position relative
    width 100%
    height 100%
    .container
      width 100%
      height 100%
      background url("../img/bg.png") no-repeat center 260px
      ul.list
        position relative
        top 200px
        left 50px
        li.item-li
          position relative
          float left
          margin 0 56px
          &:nth-child(1)
            margin-left 42px
            margin-right 144px
            ul.pop
              left -30px
          &:nth-child(2)
            ul.pop
              left -240px
          &:nth-child(3)
            ul.pop
              left -300px
          &:nth-child(4)
            ul.pop
              left -420px
          .fade-enter-active, .fade-leave-active
            transition opacity .3s
          .fade-enter, .fade-leave-to
            opacity 0
          .inner
            position relative
            &.active
              .circle
                background-color #fff
                box-shadow 0 0 10px #00aeff
            .time
              color #9aedfb
              font-size 0.14rem
              line-height 0.24rem
              text-align center
            .icon
              display block
              width 21px
              height 15px
              background url("../img/icon.png") no-repeat
              margin 0 auto
            .date
              color #9aedfb
              font-size 0.14rem
              line-height 0.24rem
            .circle
              display block
              position absolute
              bottom -19px
              left 50%
              transform translate(-50%, 0)
              width 15px
              height 15px
              border-radius 50%
              border 3px solid #0077ff
              background-color #091835
              cursor pointer

          .arrow
            position absolute
            top -18px
            left 50%
            z-index 1
            transform translate(-50%, 0)
            border-top 12px solid #2295e0
            border-bottom 12px solid transparent
            border-left 10px solid transparent
            border-right 10px solid transparent
          ul.pop
            position absolute
            top -190px
            width 553px
            height 177px
            box-sizing border-box
            padding 20px 30px
            background url("../img/pop-bg.png") no-repeat
            li.pop-li
              float left
              display flex
              width 50%
              font-size 0.14rem
              line-height 34px
              .name
                width 70px
                background linear-gradient(to bottom, #fff779, #ff8225)
                -webkit-background-clip text
                color transparent
              .value
                width 170px
                color #fff
                overflow hidden
                text-overflow ellipsis
                white-space nowrap
</style>
